.range

    &__bar
        background-color: $accent-50
        width: 100%
        height: 8px
        border-radius: 4px
        position: relative
        overflow: hidden
        margin-right: 12px
        font-size: 14px
        color: $base-text

    &__bar-inner
        border-radius: 4px
        height: 100%
        width: 100%
        background-color: $accent
        position: absolute
        content: ''
        left: -100%
        top: 0

    &__bar-wrapper
        position: relative

    &__bar-handler
        position: absolute
        background-color: #ffffff
        content: ''
        width: 20px
        height: 20px
        border-radius: 50%
        border: 4px solid $accent
        top: -6px
        left: -6px
        cursor: pointer

    &__vertical-bar
        display: flex
        justify-content: space-between
        align-items: flex-end
        margin-bottom: -2px
        padding-left: 2px
        padding-right: 2px

    &__vertical-bar-item
        width: 4px
        border-radius: 4px
        background-color: $accent-50

        &.active
            background-color: $accent

    &__labels
        display: flex
        justify-content: space-between
        color: $secondary-text
        font-family: $regular
        font-size: 12px
        line-height: 20px
        padding-top: 12px


    &__label-item

        &--center
            margin-left: -32px

    
    