.range-block
    padding: 24px
    border-radius: 12px
    border: 1px solid $border-color

    &__title
        margin-bottom: 16px

    &__button
        margin-top: 24px

    &__field
        margin-top: 16px
        margin-bottom: 20px

    &__result
        display: flex
        justify-content: space-between
        align-items: baseline

    &__result-item
        font-size: 16px
        line-height: 24px
        font-family: $semibold
        color: $secondary-text


        &--lg
            font-size: 24px
            line-height: 32px
            font-family: $base-text
            font-family: $semibold