.banner
    display: flex
    align-items: center
    padding-top: 8px
    padding-bottom: 8px
    background-color: #EBEFFF
    position: relative
    text-decoration: none

    &:hover
        text-decoration: none !important
        
        .banner__content
            opacity: .5

    &__content
        display: flex
        align-items: center
        margin-left: auto
        margin-right: auto
        font-family: $semibold
        font-size: 14px
        line-height: 20px
        color: $base-text
        text-decoration: none
        transition: .3s ease-in

       

    &__icon
        display: flex
        align-items: center
        margin-right: 4px

    &__close
        width: 16px
        height: 16px
        position: absolute
        content: ''
        right: 24px
        top: 10px
        display: flex
        justify-content: center
        align-items: center
        padding: 0
        background-color: transparent
        border: none
        cursor: pointer

        &:hover
            opacity: .4

        svg
            width: 100%
            height: 100%
            