.header-account
    border-bottom: 1px solid $border-color

    &__container
        padding: 24px
        display: flex
        justify-content: space-between

    &__logo
        display: flex
        align-items: center

        img
            display: block

    &__nav
        display: flex
        margin: 0
        padding: 0
        list-style: none
        align-items: center

    &__nav-item
        margin-top: 0

    .header-account__nav-link
        display: flex
        font-size: 16px
        line-height: 24px
        color: $secondary-text
        padding-left: 20px
        padding-right: 20px
        padding-top: 4px
        padding-bottom: 4px
        text-decoration: none
        font-family: $semibold
        transition: 0.2s ease-in

        &:hover
            color: $base-text 
            text-decoration: none

        &:focus
            text-decoration: none

        // &:visited
        //     color: unset

        &.active
            color: $accent
            text-decoration: none


    &__user-block
        display: flex
        column-gap: 12px

    &__dropdown-item
        position: relative

        &:hover
            .header-account__dropdown
                display: block
                position: absolute
                content: ''
                top: 100%
                right: 0
                    


    &__dropdown
        padding-top: 10px
        display: none
        width: 210px
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08)