.page--dark
    background-color: $dark-bg
    color: $base-text-dark

    // Хэдер

    .header-account
        border-color: $border-color-dark

    .header-account__nav-link
        color: $secondary-text-dark

        &:hover
            color: $base-text-dark

        &.active
            color: $accent-dark
            text-decoration: none

    // Кнопки

    .button
        background-color: $accent-dark

        &:hover
            background-color: $accent-dark-hover

        &:disabled
            background-color: $border-color-dark

    .button-icon
        background-color: transparent
        border-color: $border-color-dark
        color: $secondary-text-dark
        transition: .2s ease-in

        svg
            fill: $secondary-text-dark

        &:hover
            background-color: $dark-bg-l2

        &--accent
            background-color: $accent-dark

            &:hover
                background-color: $accent-dark-hover
                border-color: $accent-dark-hover

            svg
                fill: $base-text-dark

        &--secondary
            background-color: $dark-bg-l3

        


    .button-wallet
        background-color: transparent
        border-color: $border-color-dark
        color: $secondary-text-dark

        svg
            fill: $secondary-text-dark

        &:hover
            background-color: $dark-bg-l2

    .button--secondary
        color: $base-text-dark
        background-color: $dark-bg-l3
        border-color: $dark-bg-l3
        transition: .2s ease-in


        &:hover
            background-color: $dark-bg-l3-hover
            border-color: $dark-bg-l3-hover

        .button__icon

            svg
                fill: $base-text-dark

    .button--tretiary
        color: $secondary-text-dark
        background-color: transparent

        &:hover
            background-color: $dark-bg-l3
            border-color: $dark-bg-l3

    .button--warning
        background-color: $error-bg-dark

        &:hover
            background-color: $error-bg-hover-dark

    .button-table
        background-color: transparent
        border: 1px solid $border-color-dark
        
        &:hover
            background-color: $dark-bg-l3


    // Выпадающее в шапке
    
    .dropdown-menu
        background-color: $dark-bg-l2
        
        a
            color: $secondary-text-dark

            &:hover
                background-color: $dark-bg-l3

        svg
            fill: $secondary-text-dark

    // Хлебные крошки

    .breadcrumbs
        color: $secondary-text-dark

        svg
            fill: $secondary-text-dark

        a:hover
            color:$accent-dark

    // Загрузка файла
    
    .upload
        background-color: $dark-bg-l2
        border-color: $border-color-dark

        svg
            fill: $base-text-dark

        .upload__description
            color: $secondary-text-dark
    
    .upload__process
        background-color: $accent-bg-dark

    // Аккаунт

    .account__section-description--light
        color: $secondary-text-dark

    .account__section-separate::after
        background-color: $border-color-dark
        

    .account__section-separate-text
        background-color: $dark-bg

    .account__text
        background-color: $dark-bg-l2
        border: none

    .account__sidebar
        background-color: $dark-bg-l2
        border: none

    .account__report-header
        border-color: $border-color-dark

    .question-list a
        color: $secondary-text-dark
        

    .question-list a:hover
        background-color: $accent-bg-dark
        color: $base-text-dark

    .question-list a.active
        background-color: $accent-bg-dark
        color: $base-text-dark
        

        &::before
            background-color: $accent-dark


    // Инпут
    
    .form-input, .checkbox-field__inner
        background-color: $input-bg-dark
        border-color: $border-color-dark
        color: $base-text-dark

        &:hover
            border-color: $base-text-dark

    .form-label
        color: #ffffff

    .checkbox-field__text
        color: $base-text-dark

    .checkbox-field__icon
        border-color: $border-color-dark

    .checkbox-field__input:checked + .checkbox-field__inner
        border-color: $accent-dark

    .checkbox-field__input:checked + .checkbox-field__inner .checkbox-field__text
        color: $accent-dark
    
    .checkbox-field__input:checked + .checkbox-field__inner .checkbox-field__icon
        border-color: $accent-dark
        background-color: $accent-dark

    .field-dropdown__label
        color: $base-text-dark

    .field-dropdown__input
        background-color: $input-bg-dark
        border-color: $border-color-dark
        color: $base-text-dark

        &:hover
            border-color: $base-text-dark

    .field-dropdown__inner
        background-color: $input-bg-dark
        border-color: $border-color-dark
        color: $base-text-dark

    .field-dropdown__item-button
        color: $base-text-dark

        &:hover
            background-color: $dark-bg-l3

    .form-checkbox .form-icon
        background-color: $input-bg-dark
        border-color: $border-color-dark

    // Кнопки

    .tab
        border-color: $border-color-dark

    .tab__item a
        color: $secondary-text-dark

        &:hover
            color: $accent-dark

    .tab__item a.active
        color: $accent-dark
        border-color: $accent-dark

    
    // Блок

    .block
        background-color: $dark-bg-l2
        border-color: $dark-bg-l2


    // Таблица

    .table
        background-color: $dark-bg-l2
        border: none

        thead th, tbody tr
            border-color: $border-color-dark

        thead
            color: $secondary-text-dark

        tbody
            color: $base-text-dark

    .table__pagination
        border-color: $border-color-dark


    // Тэги

    .tag
        border-color: $border-color-dark
        background-color: transparent

        &.active
            border-color: $accent-dark
            background-color: $accent-bg-dark

        &[tabindex]:hover
            background-color: $dark-bg-l3-hover


    .tag--value
        color: $accent-dark

    .select-block__wrapper
        border-color: $border-color-dark

    .select-block__inner
        scrollbar-color: $secondary-text $border-color-dark

        &::-webkit-scrollbar
            background-color: $border-color-dark

    // Тэг Code

    code
        background-color: $input-bg-dark
        border-color: $border-color-dark
        color: $secondary-text-dark

    // Новая задача

    .steps
        background-color: $dark-bg-l2
        border: none

        .account__section-separate-text
            background-color: $dark-bg-l2

    .steps__header
        border-color: $border-color-dark

    .steps__body-row
        border-color: $border-color-dark

    .steps__check
        scrollbar-color: $secondary-text $border-color-dark

        &::-webkit-scrollbar
            background-color: $border-color-dark

    .step-item
        color: $secondary-text-dark
        border-color: $border-color-dark

    .step-item__icon svg
        fill: $secondary-text-dark

    .step-item--current
        border-color: $accent-dark
        background-color: $accent-bg-dark
        color: $accent-dark

        .step-item__icon svg
            fill: $accent-dark
        
    .step-item--done
        color: $secondary-text-dark

        &:hover
            border-color: $accent-dark

        svg
            fill: $accent-dark


    // Список 
    .result-list
        border-color: $border-color-dark

    .result-list__item
        color: $secondary-text-dark

    .result-list__item:last-child
        color: $base-text-dark
        
            
    // Загрузчик файла
    
    .file-loader
        background-color: $accent-bg-dark

    // Файл и описание

    .badge-item__title
        color: $base-text-dark

    .badge-item__description
        color: $secondary-text-dark


    // Статус

    .status-item__data-state
        color: $secondary-text-dark

    .status-item__data-state
        color: $base-text-dark

    .status-item--error .status-item__icon svg
        fill: $error-color-dark

    .status-item--error .status-item__data-state
        color: $error-color-dark

    .status-item--check .status-item__icon svg
        fill: $done-color-dark

    .status-item--check .status-item__data-state
        color: $done-color-dark
        


    .status--pending
        background-color: $warning-bg-color-dark 
        color: $warning-color-dark

        .status__indicator
            background-color: $warning-color-dark

    .status--done
        background-color: $done-bg-color-dark
        color: $done-color-dark

        .status__indicator
            background-color: $done-color-dark

    .status--cancel
        background-color: $error-bg-color-dark
        color: $error-color-dark

        .status__indicator
            background-color: $error-color-dark

    .status--test
        background-color: #363636
        color: $secondary-text-dark

        .status__indicator
            background-color: $secondary-text-dark


    // Пагинация

    .pagination__nav-link
        color: $secondary-text-dark

        &:hover
            background-color: $dark-bg-l3
            

    .pagination__nav-link.active
        background-color: $dark-bg-l3
        color: $accent-dark

    
    // Карточка цены

    .price-card
        background-color: $dark-bg-l2
        border-color: $dark-bg-l2

    .price-card__footer-list-item--false
        color: $secondary-text-dark 
        

    

    .price-card--accent
        border-color: $accent-dark

    .price-card__header, .price-card__footer
        border-color: $border-color-dark

    .price-card__description
        color: $secondary-text-dark
        

    .price-card__footer-list-item::before
        background-image: url('../img/icons/u_check-dark.svg')

    .price-card__footer-list-item--false::before
        background-image: url('../img/icons/u_none-dark.svg')

    // Карточка антивируса

    .antivirus-card
        background-color: $dark-bg-l2
        border-color: $border-color-dark
    
    .antivirus-card__subtitle
        color: $secondary-text-dark

    .antivirus-card.active
        background-color: $accent-bg-dark
        border-color: $accent-dark

    .antivirus-card__remove svg
        fill: $secondary-text-dark


    // Блок выбора времени подключения

    .range-block
        background-color: $dark-bg-l2
        border-color: transparent

    .range-block__result
        color: $secondary-text-dark

    .range-block__result-item--lg
        color: $base-text-dark

    .range__bar
        background-color: #333333

    .range__vertical-bar-item
        background-color: #333333

    .range__vertical-bar-item.active
        background-color: $accent-dark

    .range__bar-inner
        background-color: $accent-dark

    .range__bar-handler
        border-color: $accent-dark

    .range__label-item
        color: $secondary-text-dark


    // VNC player
    .vnc__client
        color: $base-text-dark

    .vnc__client--bit
        color: $secondary-text-dark

    .vnc__toolbar
        background-color: $dark-bg-l2

    .tooltip-block
        background-color: $dark-bg-l3

        color: #ffffff

        .text-secondary
            color: $secondary-text-dark

        .text-dark
            color: #ffffff

    // Баланс

    .balance
        background-color: $dark-bg-l2
        border-color: $dark-bg-l2

    .balance__data-icon
        background-color: $dark-bg-l3

        svg
            fill: $accent-dark

    .balance__data-value
        color: $secondary-text-dark

    .balance__data-account
        color: $base-text-dark

    
    // Карточка валюты

    .currency-card
        background-color: $dark-bg-l2
        border-color: $dark-bg-l2
        

        &:hover
            background-color: $dark-bg-l3
            border-color: $dark-bg-l3

    .currency-card__title
        color: $base-text-dark


    // Блок скриншотов

    .screenshot-list.active
        background-color: $dark-bg-l3

    .screenshot-list::before
        background-color: $dark-bg-l2

    .screenshot-list.active::before
        background-color: $dark-bg-l3

    .screenshot-list__button
        background-color: transparent
        border: 1px solid $border-color-dark
        
        &:hover
            background-color: $dark-bg-l3

        svg
            fill: $secondary-text-dark

    .screenshot-list.active .screenshot-list__button
        border: 1px solid $secondary-text-dark

        &:hover
            background-color: $secondary-text-dark

            svg
                fill: $base-text-dark


    .current-tariff
        background-color: $dark-bg-l2
        border-color: $dark-bg-l2

    .current-tariff__title
        color: $accent-dark


    .current-tariff__date
        color: $base-text-dark

    .current-tariff__date-title
        color: $secondary-text


    // Блок Пресетов

    .presets-block
        background-color: $dark-bg-l2
        border-color: $border-color-dark

    .preset-item
        background-color: $accent-bg-dark
        color: $accent-dark

        svg
            fill: $accent-dark

    // Поле адреса

    .address__label
        color: $secondary-text-dark

    .address__value
        color: $base-text-dark

    // Блок результата обработки файлов

    .result-block
        background-color: $dark-bg-l2
        border-color: $dark-bg-l2

    .result-block__dropdown-button
        color: $secondary-text-dark

        svg
            fill: $secondary-text-dark


    // Модальное окно

    .modal-container
        background-color: $dark-bg-l2
        box-shadow: none

    .modal-overlay
        background-color: #17131E

    .modal-title
        color: $base-text-dark


    .social-links a
        border-color: $border-color-dark

        &:hover
            background-color: $dark-bg-l3-hover
        
        svg
            fill: #ffffff


    .block
        .social-links__link
            color: #FCFCFC
            background-color: #3E3E3E
            border-color: #3E3E3E


            &:hover

                svg
                    fill: $base-text-dark

    .banner
        background-color: $accent-bg-dark
        color: $base-text-dark

    .banner__content
        color: $base-text-dark

    .banner__close svg

        fill: $base-text-dark
