.question-list
    list-style: none
    margin: 0
    padding: 0

    

    a
        font-size: 14px 
        line-height: 20px
        display: flex
        padding-left: 12px
        padding-right: 12px
        padding-top: 6px
        padding-bottom: 6px
        border-radius: 6px
        color: $base-text
        text-decoration: none
        position: relative

        &.active
            background-color: #FAFAFA
            
            &::before
                position: absolute
                content: ''
                left: 0
                top: 0
                bottom: 0
                width: 2px
                background-color: $accent

        &:hover
            background-color: #FAFAFA