.dropdown-menu
    display: flex
    flex-direction: column
    row-gap: 4px    
    list-style: none
    margin: 0
    padding: 16px
    border-radius: 8px
    background-color: #ffffff

    li
        margin: 0

    a
        display: flex
        column-gap: 10px
        padding-left: 12px
        padding-right: 12px
        padding-top: 8px
        padding-bottom: 8px
        color: $base-text
        font-family: $semibold
        border-radius: 4px

        &:hover
            background-color: $tretiary-hover
            text-decoration: none

        &:focus
            text-decoration: none

    &__link-icon
        width: 20px
        height: 20px
        display: flex
        justify-content: center
        align-content: center

        svg
            fill: $base-text