.pagination
    display: flex
    align-items: center
    justify-content: space-between

    &__nav
        display: flex
        align-items: center
        justify-content: center
        column-gap: 2px

        a
            color: $secondary-text

    &__nav-link
        font-size: 14px
        color: $secondary-text 
        line-height: 20px 
        width: 40px
        height: 40px
        display: flex
        justify-content: center
        align-items: center
        border-radius: 8px
        text-decoration: none
        transition: .2s ease-in
        font-family: $semibold
        
        

        &:hover
            background-color: $tretiary-hover
            text-decoration: none

        &.active
            background-color: $accent-50
            color: $accent
            text-decoration: none

            &:hover
                background-color: $accent-50
                color: $accent

    &__nav-separator
        width: 40px
        height: 40px
        display: flex
        justify-content: center
        align-items: center
        font-size: 14px
        color: $secondary-text
        line-height: 20px 

