.table
    border-radius: 12px
    border: 1px solid $border-color 
    overflow: hidden

    

    &__inner
        width: 100%
        table-layout: fixed
        border-collapse: collapse
        box-sizing: border-box

        &--auto-layout
            table-layout: auto


    thead
        font-size: 14px
        line-height: 18px
        color: $secondary-text
        

        th
            font-family: $semibold
            font-weight: normal
            text-align: left
            padding-top: 13px
            padding-bottom: 13px
            padding-left: 24px
            padding-right: 24px
            border-bottom: 1px solid $border-color
            box-sizing: border-box

            

    tbody
        font-size: 16px
        line-height: 24px
        color: $base-text
        

        td
            padding-left: 24px
            padding-right: 24px
            padding-top: 24px
            padding-bottom: 24px
            border-collapse: collapse
            border-bottom: none
            font-family: $semibold
            box-sizing: border-box

        tr:not(:last-child)
            border-bottom: 1px solid $border-color

    &__pagination
        padding-top: 16px
        padding-bottom: 16px
        padding-left: 24px
        padding-right: 24px
        border-top: 1px solid $border-color


    th.table--align-right,
    td.table--align-right
        text-align: right
        

    &__row
        display: flex
        column-gap: 12px

        &--right
            justify-content: flex-end

    .table--screenshot
        padding-top: 11px
        padding-bottom: 11px
        position: relative
    
    .td-gray
        color: #BDBDBD
        display: inline-block
        margin-left: 12px