.button-wallet
    height: 42px
    border: 1px solid $border-color
    background-color: transparent
    color: $base-text
    background-color: #ffffff
    font-size: 16px
    line-height: 24px
    display: flex
    justify-content: center
    align-items: center
    font-family: $semibold
    border-radius: 8px
    cursor: pointer
    padding-left: 12px
    padding-right: 12px

    &:hover
        background-color: $tretiary-hover

    &__icon
        width: 24px
        height: 24px
        margin-right: 8px

        svg
            width: 100%
            height: 100%
            fill: $base-text