.field-dropdown
    width: 100%


    &__input
        display: flex
        justify-content: space-between
        align-items: center
        font-size: 14px
        line-height: 20px
        color: $base-text
        padding-left: 16px
        padding-right: 12px
        padding-top: 5px
        padding-bottom: 5px
        height: 44px
        border: 1px solid $border-color
        font-family: $regular
        border-radius: 8px
        width: 100%
        box-sizing: border-box
        cursor: pointer


        &:hover
            border-color: $base-text

    &__input-value
        display: flex

    &__input-value-item
        padding-left: 4px
        color: $secondary-text

    &__input-data
        font-family: $semibold
        color: $accent
        margin-left: auto

    &__input-icon 
        width: 20px
        height: 20px
        margin-left: 12px

        svg
            fill: #98A2B3

    &__label
        display: block
        text-align: left
        font-size: 14px
        font-family: $semibold
        color: $base-text
        line-height: 20px
        margin-bottom: 8px

    &__logo
        display: flex
        align-items: center
        column-gap: 10px

        img
            width: 24px
            height: 24px
            display: block

    &__icon
        width: 20px
        height: 20px
        display: flex
        justify-content: center
        align-items: center

        svg
            display: block
            width: 100%
            height: 100%
            fill: $secondary-text

    &__wrapper
        position: relative

    &__inner
        border: 1px solid $border-color
        border-radius: 8px
        max-height: 220px
        overflow-y: auto
        position: absolute
        top: 0
        left: 0
        width: 100%
        background-color: #ffffff
        z-index: 1

    &__item-inner
        padding-left: 16px
        padding-right: 16px

        .field-dropdown__item-button
            border-radius: 4px
            color: $secondary-text

    &__item-button
        font-size: 14px
        line-height: 20px
        color: $base-text
        padding-left: 16px
        padding-right: 16px
        padding-top: 12px
        padding-bottom: 12px
        font-family: $regular
        text-align: left
        cursor: pointer
        background-color: transparent
        border: none
        width: 100%
        display: flex
        justify-content: space-between

        &:hover
            background-color: $tretiary-hover

    &__item-button-value
        font-family: $semibold
        color: $base-text
        