.badge-item
    display: flex
    align-items: center

    &__icon
        display: flex
        width: 30px
        height: 40px
        justify-content: center
        align-items: center
        margin-right: 12px

        img
            width: 100%
            height: auto

    &__title 
        font-size: 16px
        line-height: 20px
        color: $base-text
        font-family: $semibold

    &__description
        font-size: 14px
        line-height: 20px   
        color: $secondary-text
        margin-top: 2px
        font-family: $semibold

    &--large
        .badge-item__icon
            width: 48px
            height: 64px
            margin-right: 20px

        .badge-item__title
            font-size: 24px
            line-height: 36px

        .badge-item__description
            font-size: 16px
            line-height: 24px