.footer
    background-color: #F9FAFB

    &__top
        display: flex
        justify-content: space-between
        align-items: center
        padding-top: 44px
        padding-bottom: 44px

    &__bottom
        padding-top: 44px
        padding-bottom: 44px
        display: flex
        justify-content: space-between
        border-top: 1px solid $border-color
        align-items: center

    &__logo

        img
            display: block
            max-height: 32px
            width: auto
            max-width: 100%

    &__copyright
        font-size: 14px
        line-height: 20px
        color: $secondary-text
            