.page

    .modal
        align-items: flex-start
        padding-top: 80px
        padding-bottom: 80px
        overflow-y: auto
        background-color: rgba(23, 19, 30, .75)

    .modal-container
        padding: 32px
        border-radius: 16px
        position: relative
        
    .modal-header
        padding: 0
        margin-bottom: 24px

    .modal-title
        font-size: 24px
        line-height: 32px
        font-family: $semibold
        color: $base-text

    .modal-body
        padding: 0
        overflow: unset

    .modal-button
        margin-top: 24px

    .modal.active .modal-overlay, .modal:target .modal-overlay
        background-color: transparent

    .modal-footer
        padding-bottom: 0
        padding-top: 24px
        padding-left: 0
        padding-right: 0

    .modal-footer-space-between 
        display: flex
        justify-content: space-between

    .modal-footer-text
        text-align: center
        color: $secondary-text

    .modal-container
        max-width: 500px
        max-height: none

    .btn-clear
        width: 32px
        height: 32px
        display: flex
        justify-content: center
        align-items: center
        position: absolute
        right: 16px
        top: 16px
        content: ''
        margin-left: 0

        &:hover
            svg
                fill: $base-text

        svg
            fill: $tretiary-text
            transition: fill .3s ease-in

        &::before
            display: none