.button
    display: flex
    align-items: center
    column-gap: 10px
    justify-content: center
    user-select: none
    touch-action: manipulation
    position: relative
    background-color: $accent
    font-family: $semibold
    font-size: 18px
    line-height: 24px
    vertical-align: middle
    text-align: center
    white-space: normal
    word-wrap: normal
    border: none
    box-sizing: border-box
    border-radius: 8px
    color: #ffffff
    cursor: pointer
    padding-left: 36px
    padding-right: 36px
    padding-top: 16px
    padding-bottom: 16px
    cursor: pointer
    transition: background-color .3s
    white-space: nowrap
    text-decoration: none
    letter-spacing: 0.02em
    transition: background .2s ease-in
    text-decoration: none

    &:hover
        background-color: $accent-hover
        text-decoration: none
        color: #ffffff

    &:focus
        outline: 0
        box-shadow: 0 0 0 3px rgba(116, 45, 219, .5)
        text-decoration: none

    &:visited
        color: #ffffff
        text-decoration: none


    &__icon
        width: 24px
        height: 24px

        svg
            fill: #ffffff

        img
            width: 100%
            height: auto

    &__data
        display: flex
        align-items: center
        column-gap: 12px

    &__data-icon
        width: 24px
        height: 24px
        


    &--full
        width: 100%

    &--xs
        font-size: 14px
        line-height: 16px   
        padding-top: 7px
        padding-bottom: 7px
        padding-left: 15px
        padding-right: 15px

        .button__icon
            width: 16px
            height: 16px

            img
                width: 100%
                height: auto

    &--small
        font-size: 16px
        line-height: 20px
        padding-top: 10px
        padding-bottom: 10px
        padding-left: 20px
        padding-right: 20px
        font-family: $semibold

        .button__icon
            width: 20px
            height: 20px
            display: flex
            justify-content: center
            align-items: center

    &--medium
        font-size: 16px
        line-height: 20px
        padding-top: 11px
        padding-bottom: 11px
        padding-left: 24px
        padding-right: 24px
        font-family: $semibold
        column-gap: 10px

    &--secondary
        border: 1px solid $border-color
        background-color: transparent
        color: $base-text

        .button__icon
            svg
                fill: $base-text

        &:hover
            background-color: $tretiary-hover
            color: $base-text

        &:visited
            color: $base-text
            text-decoration: none

    &--tretiary
        background-color: transparent
        color: $base-text

        &:hover
            background-color: $tretiary-hover
            color: $base-text

        &:visited
            color: $base-text
            text-decoration: none

    &--warning
        background-color: #FFCDCD
        color: #D92D20

        &:hover
            background-color: #FFB2B2
            color: #D92D20

        &:visited
            color: #D92D20
            text-decoration: none

    &--language
        color: $secondary-text

        &:hover
            color: $secondary-text

        &:visited
            color: $secondary-text
            text-decoration: none
        

        .button__icon
            border-radius: 4px
            overflow: hidden
            


    &--icon
        width: 42px
        height: 42px


        svg
            width: 24px
            height: 24px


    &:disabled
        background-color: $border-color
        color: $tretiary-text

        cursor: default

        svg
            fill: $tretiary-text !important



