.tooltip
    background-color: $base-text
    color: #ffffff
    padding-top: 8px
    padding-bottom: 8px
    padding-left: 12px
    padding-right: 12px
    border-radius: 8px
    position: absolute
    max-width: 200px


    &--bottom
        top: calc(100% + 10px)
        left: 50%
        transform: translateX(-50%)
        animation: show .2s ease 


    &__title
        font-size: 12px 
        line-height: 18px
        color: #ffffff
        font-family: $semibold
        text-align: center

    &__data
        margin-top: 8px


@keyframes show
    0%
        opacity: 0
    100%
        opacity: 1