.result-block

    padding: 24px
    border-radius: 12px
    border: 1px solid $border-color
    
    &__footer
        padding-top: 12px

    &__buttons
        display: flex
        column-gap: 12px

    &__dropdown
        padding-bottom: 20px
        padding-top: 16px

    &__dropdown-button
        display: flex
        align-items: center
        width: 100%
        background-color: transparent
        border: none
        padding: 0
        font-family: $semibold
        color: $secondary-text
        font-size: 16px
        line-height: 24px
        cursor: pointer
        
        &:hover
            color: $base-text

        &.active

            + .result-block__dropdown-content
                display: block

            .result-block__dropdown-icon
                transform: rotate(-180deg) 

    &__dropdown-content
        display: none
        margin-top: 12px

    &__dropdown-row
        margin-bottom: 12px

        &:last-child
            margin-bottom: 0


    &__dropdown-icon 
        width: 16px
        height: 16px
        display: flex
        margin-left: auto

        svg
            width: 100%
            height: 100%
            fill: $secondary-text

    
