.step-item
    padding-left: 10px
    padding-top: 7px
    padding-bottom: 7px
    padding-right: 16px
    border: 1px solid $border-color
    border-radius: 100px
    display: flex
    justify-content: center
    flex-grow: 0
    align-items: center
    column-gap: 8px
    font-family: $semibold
    color: $secondary-text
    cursor: default

    &__icon
        width: 24px
        height: 24px
        display: flex
        justify-content: center
        align-content: center

        svg
            width: 100%
            height: 100%
            fill: $secondary-text

        &--done
            display: none

    &__text
        display: flex
        align-items: center
        font-size: 14px
        white-space: nowrap

    &__text-number
        padding-right: 8px

    &__separator
        display: flex
        align-items: center
        width: 20px
        height: 20px
        margin-left: auto
        margin-right: auto

        svg
            fill: $secondary-text
            width: 100%
            height: 100%
        

    &--current
        color: $accent
        border: 1px solid $accent
        background-color: $accent-50

        svg
            fill: $accent

    &--done
        cursor: pointer

        &:hover
            border-color: $accent

        .step-item__icon
            display: none
            svg
                fill: $accent

        .step-item__icon--done
            display: flex
