.feature-item-section
    padding-top: 80px
    padding-bottom: 80px

    &__container
        display: grid
        grid-template-columns: 1fr 1fr
        column-gap: 40px

    &__text
        padding-right: 56px
        display: flex
        flex-direction: column
        justify-content: center
        align-items: flex-start

        &--right
            padding-left: 56px
            padding-right: 0
    
    &__image
        padding-top: 64px
        padding-bottom: 64px
        overflow: hidden
        background-color: #EAECF2
        border-radius: 8px
        display: flex
        background-image: url('../img/decor/decor-bg.svg')
        background-position: 0 0
        background-size: cover
        background-repeat: no-repeat
        background-attachment: fixed


        &--left
            justify-content: flex-end

            .feature-item-section__image-inner
                margin-left: 0
                margin-right: 48px

        &--center
            justify-content: center
            
            .feature-item-section__image-inner
                padding-left: 48px
                padding-right: 48px
                margin-left: 0
                margin-right: 0
                max-width: 100%
    
    &__image-inner
        margin-left: 48px 
        width: 720px
        flex-shrink: 0

        img
            width: 100%
            height: auto
            box-shadow: 0px 2px 20px 0px rgba(0, 0, 0, 0.10)
