.social-links
    margin: 0
    padding: 0
    list-style: none
    display: flex
    column-gap: 12px

    li
        margin-top: 0

    &__link
        width: 32px
        height: 32px
        background-color: #EFEFEF
        display: flex
        justify-content: center
        align-items: center
        border-radius: 4px
        border: 1px solid $border-color
        background-color: transparent
        transition: background .3s ease-in

        &:hover
            background-color: $base-text

            svg
                fill: #ffffff


        svg
            width: 24px
            height: 24px
            fill: $base-text
            nsition: fill .3s ease-in