.screenshot-list
    width: 100%
    overflow: hidden
    column-gap: 8px
    row-gap: 8px
    display: flex
    padding-right: 32px
    box-sizing: border-box
    position: relative
    flex-wrap: wrap
    height: 50px

    &::before
        position: absolute
        content: ''
        height: 100%
        width: 32px
        background-color: #ffffff
        right: 0
        top: 0

    img
        cursor: pointer
        transition: .2s ease
        border-radius: 4px
        overflow: hidden

        &:hover
            opacity: .8

    &__button
        width: 24px
        height: 50px
        background-color: #f0f0f0
        position: absolute
        right: 0
        top: 0
        border-radius: 4px
        display: flex
        align-items: center
        justify-content: center
        border: none
        transition: .2s ease-in
        cursor: pointer
        padding: 0

        &:hover
            background-color: $tretiary-hover


        svg
            width: 20px
            height: 20px
            fill: $base-text

    &.active
        position: absolute
        height: 220px
        padding-bottom: 16px
        left: 0
        top: 0
        padding-top: 16px   
        z-index: 2
        background-color: #ffffff
        padding-left: 24px
        overflow-x: hidden
        overflow-y: auto
        box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12)


        .screenshot-list__button
            top: 16px
            right: 7px

            svg
                transform: rotate(180deg)