.tooltip-block {
  background-color: #ffffff;
  color: #17131E;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 12px;
  padding-right: 12px;
  border-radius: 8px;
  position: absolute;
  z-index: 2;
  width: 280px;
  box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08);
  font-size: 14px;
  line-height: 16px;
  display: none;
}
.tooltip-block--top {
  bottom: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%);
  animation: show 0.2s ease;
}
.tooltip-block--bottom {
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  animation: show 0.2s ease;
}
.tooltip-block__title {
  font-size: 12px;
