  font-family: "Manrope-SemiBold", sans-serif;
  text-align: center;
}
.tooltip-block__data {
  margin-top: 8px;
}

@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
