.currency-card
    display: flex
    flex-direction: column
    align-items: center
    padding: 32px
    border-radius: 8px
    border: 1px solid #E7EAEE
    background-color: #ffffff
    width: 160px
    cursor: pointer
    transition: .2s ease-in

    &:hover
        background-color: $tretiary-hover

    &__logo
        width: 48px
        height: 48px
        margin-bottom: 24px

    &__title
        font-size: 18px
        line-height: 24px   
        text-align: center
        color: $base-text
        font-family: $semibold