.help
    display: flex
    align-items: center
    column-gap: 4px
    font-family: $semibold
    color: $secondary-text

    &__icon
        width: 20px
        height: 20px
        position: relative

        svg
            width: 100%
            height: 100%
            fill: $secondary-text

        &:hover
            .tooltip-block
                display: block

