.address

    &__label
        display: block
        text-align: left
        font-size: 14px
        font-family: $semibold
        color: $base-text
        line-height: 20px
        margin-bottom: 4px
        color: $secondary-text

    &__value
        font-size: 16px 
        line-height: 20px
        color: $base-text
        font-family: $semibold

    &__data
        display: flex
        align-items: center


    &__buttons
        display: flex
        column-gap: 16px
        margin-left: auto

    &__button-wrapper
        position: relative
        display: flex
        justify-content: center
        align-items: center

    &__button
        background-color: transparent
        padding: 0
        border: none
        cursor: pointer
        transition: opacity .2s

        &:hover
            opacity: .7

        svg
            fill: $secondary-text


        &.tooltip
            background-color: transparent
            width: 24px
            height: 24px
            padding: 0
            position: relative

            &::after
               font-size: 14px
               line-height: 20px
               font-family: $semibold
               border-radius: 4px 
                