.header

    &__container
        max-width: 1320px
        padding-left: 20px
        padding-right: 20px
        margin-left: auto
        margin-right: auto

    &__nav-bar
        padding-top: 24px
        padding-bottom: 24px
        display: flex
        justify-content: space-between
        align-items: center

        &--center
            justify-content: center

    &__logo

        img
            display: block
            max-height: 32px
            width: auto

    
    &__user-block
        display: flex
        column-gap: 16px