.page

    .form-label
        font-size: 14px
        font-family: $semibold
        color: $base-text
        line-height: 20px
        margin-bottom: 8px
        padding: 0

    .form-input
        font-size: 14px
        line-height: 20px
        color: $base-text
        padding-left: 16px
        padding-top: 12px
        padding-bottom: 12px
        height: 44px
        border: 1px solid $border-color
        border-radius: 8px
        font-family: $regular

        &::placeholder
            color: $tretiary-text

    .form-captcha
        display: flex
        flex-direction: column
        align-items: center
        padding-top: 28px

        img
            margin-bottom: 8px

        a
            font-size: 14px
            line-height: 20px

    .form-input-hint
        font-size: 14px
        line-height: 20px
        margin-bottom: 0
        margin-top: 8px

    .form-group
        margin-bottom: 16px
        

        &.disabled
            .form-label
                opacity: .4

        input:disabled:hover
            border-color: $border-color

    .form-input
        &:hover
            border: 1px solid $base-text

        &:focus
            border: 1px solid $accent
            outline: 0
            box-shadow: 0 0 0 3px rgba(116, 45, 219, .5)

    .has-error 
        .form-input-hint
            color: $error-color

        .form-input
            border: 1px solid $error-color
            background-color: #ffffff

    .form
        
        &__button
            margin-top: 24px  


.form-checkbox
    font-size: 14px
    line-height: 20px
    height: 20px
    min-height: unset
    font-family: $regular
    padding-top: 0
    padding-bottom: 0
    margin: 0

    .form-icon
        border-radius: 4px
        top: 2px
