.current-tariff
    text-align: left
    padding: 32px
    border-radius: 12px
    border: 1px solid $border-color
    transition: transform .3s ease-in
    background-color: #ffffff
    position: relative
    flex-grow: 1
    width: 100%

    &__header
        display: flex
        align-items: center
        justify-content: space-between

    &__title
        font-size: 24px
        text-transform: uppercase
        line-height: 24px
        margin: 0
        font-family: $semibold
        color: $accent

    &__body
        padding-top: 32px
        padding-bottom: 32px

    &__price
        font-size: 40px
        line-height: 48px
        margin: 0
        font-family: $semibold

        &--small
            font-size: 20px
            line-height: 28px
            margin-left: 12px
            font-family: $semibold


    &__date
        font-size: 16px
        line-height: 24px
        color: $base-text
        font-family: $semibold

    &__date-title
        font-size: 16px
        line-height: 24px
        color: $secondary-text
        font-family: $regular