.preset-item
    display: flex
    column-gap: 8px
    color: $accent
    padding-left: 16px
    padding-right: 24px
    padding-top: 10px
    padding-bottom: 10px
    background-color: $accent-50
    border-radius: 50px
    font-family: $semibold

    &__icon
        width: 24px
        height: 24px
        display: flex
        justify-content: center
        align-items: center

        svg
            fill: $accent