.status
    align-items: center
    border-radius: 16px
    padding-top: 3px
    padding-bottom: 3px
    padding-left: 8px
    padding-right: 8px
    font-size: 12px
    line-height: 16px
    font-family: $semibold
    display: inline-flex
    white-space: nowrap

    &__indicator
        width: 6px
        height: 6px
        border-radius: 50%
        margin-right: 6px 

    &--done
        color: #027A48
        background-color: #ECFDF3

        .status__indicator
            background-color: #12B76A

    &--cancel
        color: #EF3131
        background-color: #FFB2B2

        .status__indicator
            background-color: #EF3131

    &--pending
        color: #DC6803
        background-color: #FEF0C7

        .status__indicator
            background-color: #DC6803

    &--test
        color: $secondary-text
        background-color: #FAFAFA

        .status__indicator
            background-color: $secondary-text
