.vnc

    &__header
        display: flex
        justify-content: space-between
        align-items: center
        margin-bottom: 32px

    &__client
        font-size: 24px
        line-height: 26px
        color: $base-text
        display: inline-flex
        font-family: $semibold

        &--bit
            color: $secondary-text
            margin-left: 20px

    &__user-block
        display: flex
        column-gap: 32px
        align-items: center

    &__timer
        font-size: 20px
        line-height: 28px
        font-family: $semibold

    &__player
        width: 100%
        border-radius: 12px
        overflow: hidden
        
    &__video
        width: 100%
        padding-top: 62.5%
        background-color: $secondary-text

    &__toolbar
        padding: 24px
        display: flex
        background-color: $base-text

    &__toolbar-fullscreen
        margin-left: auto
    
