.header-link 
    position: relative
    
    &:hover

        .header-link__link
            display: flex   
            

    &__link
        display: none
        position: absolute
        content: ''
        left: 100%
        padding-left: 8px
        top: 50%
        transform: translateY(-50%)
        width: 28px
        justify-content: center
        align-content: center

        svg
            width: 100%
            height: 100%
            fill: $accent