.price-card
    text-align: left
    padding: 40px
    border-radius: 16px
    border: 1px solid $border-color
    transition: transform .3s ease-in
    background-color: #ffffff

    &:hover
        transform: scale(1.02)

    &__header
        padding-bottom: 32px
        border-bottom: 1px solid $border-color

    &__title
        font-size: 28px
        line-height: 36px
        margin-bottom: 12px
        font-family: $semibold

    &__description
        height: 48px
        text-overflow: ellipsis
        margin-bottom: 0
        font-size: 16px
        line-height: 24px
        color: $secondary-text

    &__body
        padding-top: 32px
        padding-bottom: 32px
        font-family: $semibold

    &__price
        font-size: 56px
        line-height: 64px
        margin-bottom: 12px

        &--small
            font-size: 24px
            line-height: 28px
            margin-left: 12px
            text-transform: uppercase


    &__price-description
        font-size: 16px
        line-height: 24px
        font-weight: 500
        margin-bottom: 0
        height: 48px
        text-overflow: ellipsis

    &__body-button 
        padding-top: 32px


    &__footer
        padding-top: 32px
        border-top: 1px solid $border-color

    &__footer-list
        margin: 0
        padding: 0
        list-style: none

    &__footer-list-item
        padding-left: 34px
        position: relative
        margin-top: 0

        &::before
            position: absolute
            content: ''
            width: 24px
            height: 24px
            background: url('../img/icons/u_check.svg') no-repeat center
            background-size: cover
            left: 0
            top: 0


        &--false 
            color: $tretiary-text
            
            &:before
                background: url('../img/icons/u_none.svg') no-repeat center

        &:not(:first-child)
            margin-top: 16px


    &--accent
        border: 4px solid $accent

