.balance
    padding: 30px
    border: 1px solid $border-color
    border-radius: 12px
    width: 100%

    &__data
        display: flex
        margin-right: 24px

    &__data-icon
        width: 48px
        height: 48px
        display: flex
        justify-content: center
        align-items: center
        background-color: #F9FAFB
        border-radius: 4px
        margin-right: 24px

        svg
            width: 24px
            height: 24px
            fill: $accent

    &__button
        margin-top: 24px


    &__data-value
        font-size: 16px
        line-height: 24px   
        color: $secondary-text
        font-family: $semibold
        flex-shrink: 0
        min-width: 132px

    &__data-account
        font-size: 32px
        line-height: 40px
        color: $base-text
        margin-top: 8px
