.tooltip-block
    background-color: #ffffff
    color: $base-text
    padding-top: 12px
    padding-bottom: 12px
    padding-left: 12px
    padding-right: 12px
    border-radius: 8px
    position: absolute
    z-index: 2
    width: 280px
    box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08)
    font-size: 14px
    line-height: 16px
    display: none

    &--top
        bottom: calc(100% + 10px)
        left: 50%
        transform: translateX(-50%)
        animation: show .2s ease 

    &--bottom
        top: 100%
        left: 50%
        transform: translateX(-50%)
        animation: show .2s ease 


    &__title
        font-size: 12px 
        line-height: 18px
        color: #ffffff
        font-family: $semibold
        text-align: center

    &__data
        margin-top: 8px


@keyframes show
    0%
        opacity: 0
    100%
        opacity: 1