.file-loader
    padding: 16px
    border-radius: 8px
    background-color: $accent-50
    position: relative

    &__close
        width: 20px
        height: 20px
        display: flex
        align-items: center
        justify-content: center
        position: absolute
        content: ''
        top: 16px
        right: 16px 
        border: none
        padding: 0
        background-color: transparent
        cursor: pointer
        transition: .2s ease-in

        svg
            fill: $secondary-text

        &:hover

            opacity: .5

    &__process
        padding-left: 42px
        display: flex
        align-items: center
        margin-top: 2px
        font-family: $semibold
        

    &__process-bar
        background-color: $accent-50
        width: 100%
        height: 8px
        border-radius: 4px
        position: relative
        overflow: hidden
        margin-right: 12px
        font-size: 14px
        color: $base-text

    &__process-bar-inner
        border-radius: 4px
        height: 100%
        width: 100%
        background-color: $accent
        position: absolute
        content: ''
        left: -100%
        top: 0
