.button-table
    padding: 6px
    border: none
    display: flex
    justify-content: center
    align-items: center
    border-radius: 4px
    transition: .2s ease-in
    cursor: pointer

    &:hover
        background-color: $tretiary-hover

    svg
        width: 20px
        height: 20px
        fill: $secondary-text