.presets-block
    display: flex
    justify-content: space-between
    align-items: center
    padding-top: 16px
    padding-bottom: 16px
    padding-left: 24px
    padding-right: 24px
    border-radius: 12px
    border: 1px solid $border-color

    &__buttons
        display: flex
        align-items: center
        column-gap: 16px

    &__data
        font-size: 16px
        line-height: 24px
        font-family: $semibold