.upload
    padding: 24px
    border-radius: 20px
    border: 1px dashed $tretiary-text
    display: flex
    flex-direction: column
    align-items: center

    width: 100%
    position: relative

    &__process
        position: absolute
        content: ''
        top: 0
        bottom: 0
        right: 0
        left: 0
        background-color: #ffffff
        display: flex
        justify-content: center
        align-items: center
        z-index: 5
        border-radius: 20px
        padding-left: 24px
        padding-right: 24px



        .file-loader
            width: 100%
            background-color: transparent
            position: unset

    &__icon
        width: 24px
        height: 24px
        display: flex
        justify-content: center
        align-content: center
        margin-bottom: 16px

        svg
            fill: $base-text

    &__title
        font-size: 18px
        line-height: 28px
        font-family: $semibold
        margin-bottom: 16px
        text-align: center

    &__description
        font-size: 12px 
        line-height: 16px
        color: $secondary-text
        margin-bottom: 24px
        text-align: center

    &__field
        margin-bottom: 24px
        width: 100%


    &__button
        margin-top: auto

    .form-group
        width: 100%
        margin-bottom: 0