.missing-section

    &__container
        // +wrapper()

    &__content
        padding-top: 120px
        padding-bottom: 120px
        display: flex
        flex-direction: column
        align-items: center
        text-align: center
        column-gap: 32px
        align-items: center
        max-width: 600px
        margin-left: auto
        margin-right: auto

    &__text

        .h1
            font-size: 40px 
            line-height: 48px

        .text
            color: $secondary-text

    &__buttons
        display: flex
        justify-content: center
        column-gap: 24px   
        margin-top: 32px 

    &__social
        margin-top: 48px    
        display: flex
        flex-direction: column
        align-items: center

    &__social-label
        font-size: 14px
        line-height: 20px
        // font-family: $medium
        margin-bottom: 16px
        color: $secondary-text

    &__image
        margin-bottom: 32px
        margin-left: 24px
    