.block
    padding: 32px
    border-radius: 12px
    border: 1px solid $border-color

    &__section

        &:not(:last-child)
            margin-bottom: 48px

    &__title
        margin-bottom: 24px

    &__button
        margin-top: 24px


    .social-links__link
        &:hover
            background-color: $tretiary-hover

            svg
                fill: $base-text