.feature
    padding-right: 16px

    &__icon
        width: 64px
        height: 64px
        display: flex
        justify-content: center
        align-items: center
        margin-bottom: 24px
        border: 1px solid $border-color
        border-radius: 4px

        img
            width: 48px
            height: 48px


    &__title
        margin-bottom: 12px

    &__text
        font-size: 16px
        line-height: 24px
        color: $secondary-text
        margin-bottom: 0