.select-form
    position: relative

    &__icon
        width: 20px
        height: 20px
        display: flex
        justify-content: center
        align-items: center
        position: absolute
        right: 16px
        top: 50%
        transform: translateY(-50%)
        z-index: 2

        svg
            width: 100%
            height: 100%
            fill: $secondary-text