.select-block
    &__label
        flex: 16px
        line-height: 20px
        margin-bottom: 16px
        font-family: $semibold

    &__wrapper
        padding-top: 16px
        padding-bottom: 16px
        border: 1px solid $border-color
        border-radius: 16px
        height: 365px
        

    &__inner
        padding-left: 16px
        padding-right: 16px
        display: flex
        flex-direction: column
        height: 333px
        row-gap: 16px 
        overflow-y: auto
        scrollbar-width: 6px
        scrollbar-color: $secondary-text $border-color

        &::-webkit-scrollbar
            width: 6px
            background-color: $border-color
            border-radius: 20px
            padding-top: 16px
            padding-bottom: 16px

        &::-webkit-scrollbar-thumb
            background-color: $secondary-text
            border-radius: 20px

    &__top
        display: flex
        column-gap: 12px
