.antivirus-card
    display: flex
    align-items: center
    padding: 8px
    padding-right: 16px
    border-radius: 12px
    border: 1px solid $border-color
    transition: all .3s ease-in
    cursor: pointer

    &:hover
        transform: scale(1.05)
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.08)

    &__image
        width: 64px
        height: 64px
        display: flex
        justify-content: center
        align-items: center
        margin-right: 16px

        img
            width: 100%
            max-width: 100%
            height: auto

    &__title
        font-size: 18px
        font-family: $bold

    &__subtitle
        font-size: 16px
        line-height: 24px
        color: $secondary-text

    &.active
        border-color: $accent
        background-color: $accent-bg

    &--medium


        .antivirus-card__image
            width: 48px
            height: 48px
            margin-right: 12px

    &--small-horizontal
        padding: 16px

        .antivirus-card__image
            width: 24px
            height: 24px
            margin-right: 8px

        .antivirus-card__content
            display: flex
            align-items: baseline
            max-width: 230px
            overflow: hidden
            width: 100%

        .antivirus-card__title
            font-size: 16px
            line-height: 20px
            margin-right: 8px
        
        .antivirus-card__subtitle
            font-size: 14px
            line-height: 20px

    &--horizontal
        position: relative

        &:hover
            transform: scale(1)
            box-shadow: unset
            cursor: default

    &__checkboxes
        display: flex
        column-gap: 16px
        align-items: center
        margin-left: auto
        margin-right: auto

    &__remove
        position: absolute
        content: ''
        top: 50%
        right: 16px
        width: 20px
        height: 20px
        transform: translateY(-50%)
        cursor: pointer
        background-color: transparent
        border: none
        padding: 0
        transition: .2s ease-in
        

        &:hover
            opacity: .5

