.offer-section
    padding-bottom: 112px
    padding-top: 96px

    &__header
        text-align: center

    &__button
        display: flex
        justify-content: center

    &__button
        margin-top: 32px


    &__image-wrapper
        margin-top: 80px
        padding-top: 48px
        padding-bottom: 48px
        background-color: #EAECF2
        border-radius: 8px
        background-image: url('../img/decor/decor-bg.svg')
        background-position: 0 0
        background-size: cover
        background-repeat: no-repeat
        background-attachment: fixed

    &__image-inner
        max-width: 980px
        margin-left: auto
        margin-right: auto

        img
            display: block
            width: 100%
            height: auto
            box-shadow: 0px 2px 20px 0px rgba(0, 0, 0, 0.10)

    .supporting-text
        max-width: 768px
        margin-left: auto
        margin-right: auto
        margin-top: 24px
        