.steps
    border: 1px solid $border-color
    border-radius: 12px
    display: flex
    
    &__header
        display: flex
        flex-direction: column
        padding-top: 32px
        padding-bottom: 32px
        padding-right: 32px
        padding-left: 32px
        width: 260px
        flex-shrink: 0
        row-gap: 24px
        border-right: 1px solid $border-color 
    

    &__body
        padding: 32px
        padding-bottom: 32px
        box-sizing: border-box
        display: flex
        flex-direction: column

        &--md
            max-width: 824px
            flex-grow: 1
            margin-left: auto
            margin-right: auto

        .form-group
            margin-bottom: 16px

        .form-group:last-child
            margin-bottom: 0
            

        .checkbox-field
            margin-bottom: 16px

        .result-list
            padding-bottom: 24px
            padding-top: 0
            border-top: none

    &__body-title
        margin-bottom: 24px

    &__body-row
        padding-bottom: 24px
        border-bottom: 1px solid $border-color
        margin-bottom: 24px

    &__body-result
        .result-list
            padding-top: 24px
            padding-bottom: 0
            border-top: 1px solid $border-color

    &__presets-block
        margin-bottom: 32px

    &__select-block
        display: grid
        column-gap: 16px
        grid-template-columns: 1fr 1fr 1fr

    &__select-fields
        display: flex
        flex-direction: column
        row-gap: 20px
        margin-top: 32px
        
        .checkbox-field
            margin-bottom: 0

    &__check
        padding-right: 20px
        margin-right: -20px
        flex-grow: 1
        max-height: 435px
        overflow-y: auto
        scrollbar-width: 6px
        scrollbar-color: $secondary-text $border-color

        &::-webkit-scrollbar
            width: 6px
            background-color: $border-color
            border-radius: 20px
            padding-top: 16px
            padding-bottom: 16px

        &::-webkit-scrollbar-thumb
            background-color: $secondary-text
            border-radius: 20px

    &__check-list
        display: flex
        flex-direction: column
        row-gap: 20px

    &__nav
        padding-top: 32px   
        display: flex
        margin-top: auto


    &__nav-right
        display: flex
        column-gap: 16px
        margin-left: auto

    &__date
        display: flex
        margin-top: 38px
        justify-content: flex-end

    &__date-title
        color: $secondary-text

    &__date-data
        display: flex
        padding-left: 20px

    &__date-day
        margin-right: 8px

    &__date-time
        margin-right: 8px