.tab
    display: flex
    border-bottom: 1px solid $border-color
    column-gap: 40px  
    list-style: none
    margin-top: 0
    margin-bottom: 0  

    &__item
        margin-top: 0
        
        a
            font-size: 16px
            line-height: 24px
            padding-bottom: 16px
            padding-top: 16px
            color: $secondary-text
            text-decoration: none
            display: flex
            font-family: $semibold
            margin-bottom: -1px

            &:hover
                color: $accent


            &.active
                color: $accent
                text-decoration: none
                border-bottom: 3px solid $accent