.status-item
    display: flex
    align-items: center
    
    &__icon
        width: 20px
        height: 20px
        display: flex
        justify-content: center
        align-items: center
        margin-right: 4px

        svg
            width: 100%
            height: 100%    
    
    &__data
        display: flex
        color: $secondary-text

    &__data-state
        margin-right: 3px
        color: $base-text

    &--error

        svg
            fill: $error-color

        .status-item__data-state
            color: $error-color

    &--check
        svg
            fill: $done-color

        .status-item__data-state
            color: $done-color