.account

    &__content
        padding-top: 32px
        padding-bottom: 120px

    &__breadcrumbs
        margin-bottom: 48px 

    &__section
        margin-bottom: 64px

        &:last-child
            margin-bottom: 0

    &__container-md
        max-width: 760px
        margin-left: auto
        margin-right: auto

    &__section-row
        display: flex
        column-gap: 16px

    &__section-title
        margin-bottom: 24px

    &__section-checkbox
        margin-bottom: 20px
        display: flex
        column-gap: 20px

    &__section-description
        margin-bottom: 24px

        &:last-child
            margin-bottom: 0

        p 
            margin-bottom: 0

        h5 + p
            margin-top: 12px

        p + p
            margin-top: 8px

        
        &--light
            color: $secondary-text

        &--notice
            color: $warning-color
            font-family: $semibold

    &__section-upload
        display: flex
        column-gap: 64px
        padding-top: 16px

    &__section-separate
        font-size: 12px
        line-height: 20px
        font-family: $regular
        color: $tretiary-text
        display: flex
        align-items: center
        position: relative

        &::after
            position: absolute
            left: 50%
            content: ''
            top: 0
            bottom: 0
            width: 1px
            background-color: $border-color

    &__section-header-block
        display: flex
        column-gap: 16px
        margin-bottom: 16px

    &__section-separate-text
        background-color: #ffffff
        position: relative
        z-index: 2

    &__balance
        max-width: 332px
        width: 100%


    &__block-tab-nav
        margin-bottom: 32px

    &__block--with-sidebar
        display: flex
        column-gap: 80px
        align-items: flex-start

    &__text
        padding: 32px
        border-radius: 12px
        border: 1px solid $border-color
        flex: 0 1 780px

        .h1, .h2, .h3, .h4
            margin-bottom: 24px

        p + .h1,p + .h2,p + .h3,p + .h4
            margin-top: 40px

    &__sidebar
        padding: 32px
        border-radius: 12px
        border: 1px solid $border-color
        flex: 0 1 420px

    &__sidebar-title
        padding-left: 12px
        margin-bottom: 24px

    &__block-subscribe
        display: flex
        justify-content: space-between

    &__block-subscribe-text
        max-width: 760px
        padding-right: 40px

        p
            margin-top: 24px

    &__block-subscribe-card
        max-width: 400px
        width: 100%


    &__select
        display: grid
        grid-template-columns: 2fr 1fr
        column-gap: 32px

    &__select-list
        row-gap: 16px
        margin-bottom: 48px


    &__select-presets
        margin-bottom: 48px

    &__select-fields
        display: flex
        flex-direction: column
        row-gap: 20px

        .form-group
            margin-bottom: 0

    &__report-header
        display: flex
        justify-content: space-between
        align-items: center
        margin-bottom: 40px
        padding-bottom: 32px
        border-bottom: 1px solid $border-color

    &__report-buttons
        display: flex
        column-gap: 20px
        

    &__report-list
        max-width: 440px    

    &__report-list-title
        margin-bottom: 24px

    &__report-table
        margin-top: 48px
