.button-icon
    height: 42px
    width: 42px
    border: 1px solid $border-color
    background-color: transparent
    color: $base-text
    background-color: #ffffff
    font-size: 18px
    line-height: 24px
    display: flex
    justify-content: center
    align-items: center
    font-family: $semibold
    border-radius: 8px
    cursor: pointer
    padding-left: 6px
    padding-right: 6px
    transition: background .2s ease-in


    &:hover
        background-color: $tretiary-hover


    &:focus
        outline: 0
        box-shadow: 0 0 0 3px rgba(116, 45, 219, .5)

    &__icon
        width: 24px
        height: 24px

        svg
            width: 100%
            height: 100%
            fill: $base-text


    &--accent
        background-color: $accent
        border-color: $accent
                
        &:hover
            background-color: $accent-hover
            

        .button-icon__icon
            svg
                fill: #ffffff

