.checkbox-field
    cursor: pointer
    display: block

    &__input
        position: absolute
        width: 1px
        height: 1px
        margin: -1px
        padding: 0
        overflow: hidden
        border: 0
        clip: rect(0,0,0,0)
        -webkit-clip-path: inset(100%)
        clip-path: inset(100%)

        &:checked + .checkbox-field__inner
            border: 1px solid $accent

            .checkbox-field__icon
                border-color: $accent
                background-color: $accent

            .checkbox-field__icon--checkbox
                position: relative

                &::before
                    background-clip: padding-box
                    border: 0.1rem solid #fff
                    border-left-width: 0
                    border-top-width: 0
                    content: ""
                    height: 9px
                    left: 50%
                    margin-left: -3px
                    margin-top: -6px
                    position: absolute
                    top: 50%
                    transform: rotate(45deg)
                    width: 6px

            .checkbox-field__text
                color: $accent

        

    &__inner
        display: flex
        align-items: center
        border: 1px solid $border-color
        border-radius: 8px
        padding-top: 6px
        padding-bottom: 6px
        padding-left: 16px
        padding-right: 16px
        cursor: pointer
        height: 44px

        &:hover
            border-color: $base-text

    &__text
        font-size: 14px
        line-height: 24px
        color: $base-text
        font-family: $regular

    &__icon-wrapper
        width: 20px
        height: 20px
        box-sizing: border-box
        display: flex
        justify-content: center
        align-items: center
        margin-left: auto

    &__image
        margin-right: 10px
        width: 24px
        height: 24px

    &__icon
        border: 1px solid #bcc3ce
        flex-shrink: 0
        box-sizing: border-box
        width: 16px
        height: 16px
        border-radius: 50%

        &--checkbox
            border-radius: 4px

    &--disabled
        opacity: .5
        background-color: #F4F4F4
        pointer-events: none