@media (-webkit-device-pixel-ratio: 1.5) 
    * 
        zoom: 0.925
        font-size: 105%
        line-height: 1.3 !important
    
html
    margin: 0
    padding: 0



  


.page
    margin: 0
    padding: 0
    font-size: 16px
    line-height: 24px
    color: $base-text
    font-family: $regular

    &--unscrollable
        overflow: hidden

    .h1
        font-size: 48px
        line-height: 60px
        font-family: $bold
        margin-bottom: 24px
        font-weight: normal

    .h2
        font-size: 32px
        line-height: 40px
        font-family: $semibold
        margin-bottom: 0
        font-weight: normal

    .h3
        font-size: 24px
        line-height: 32px
        font-family: $semibold
        margin-bottom: 0
        font-weight: normal
    
    .h4
        font-size: 20px
        line-height: 28px
        font-family: $semibold
        margin-bottom: 0
        font-weight: normal

    .h5
        font-size: 16px
        line-height: 24px
        font-family: $semibold
        margin-bottom: 0
        font-weight: normal

    .subheading
        font-size: 14px
        line-height: 24px
        padding-left: 12px
        padding-right: 12px
        background-color: $accent-50
        color: $accent
        font-family: $semibold
        display: inline-block
        border-radius: 4px
        margin-top: 0
        margin-bottom: 8px

    .supporting-text
        font-size: 18px
        line-height: 28px
        color: $secondary-text
        margin-bottom: 0

    .title-lg
        font-size: 20px
        line-height: 24px
        font-family: $semibold


    code
        font-size: 16px
        line-height: 24px
        font-family: $regular
        padding: 16px   
        background-color: #FAFAFA
        border: 1px solid $border-color
        border-radius: 8px
        color: $base-text
        display: block

    code + p, p + code
        margin-top: 24px
    