.breadcrumbs
    padding: 0
    margin: 0
    display: flex
    list-style: none
    font-size: 14px
    line-height: 20px

    &__item
        color: $secondary-text
        margin: 0
        display: flex
        align-items: center

        &:not(:first-child)
            margin-left: 4px

        svg
            margin-left: 4px

        &:last-child
            svg
                display: none


    a
        color: $secondary-text
        
        &:hover
            color: $accent
            text-decoration: none
