.result-list
    display: flex
    flex-direction: column
    row-gap: 16px
    padding-top: 20px
    padding-bottom: 20px
    border-top:  1px solid $border-color

    &__row
        display: flex

    &__item
        width: 65%
        color: $secondary-text
        font-family: $semibold
        line-height: 24px

        &:last-child
            text-align: right
            color: $base-text
            width: 35%

        &--lg
            font-size: 24px
            line-height: 32px